<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process266"></a>

<p><br></p>
<p><span class="rvts17">最后一名魔导师（</span><span class="rvts50">The Last Stand of the Magisters</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:341,Y:270</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：林德</span><span class="rvts20"></span><span class="rvts21">凯姆（</span><span class="rvts20">Linder Kemm</span><span class="rvts21">）</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">找到隐藏的地下室，搜集魔导师证据，交给林德</span><span class="rvts20"></span><span class="rvts21">凯姆</span>
<br><span class="rvts39"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1461.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">该任务必须是在林德</span><span class="rvts20"></span><span class="rvts21">凯姆还存活的情况下，才可以顺利关闭，因为最终，你是需要把情报交给他的。林德凯姆就在兵营里的这个区域。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1462.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">使用源力视野可以发现兵营处有魔导师的灵魂，与他交谈，尝试说服他告诉你隐蔽兵营的入口。即便失败也没关系，我们可以自己找到。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1463.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">在兵营外围（下水道入口）处，可以找到另一个魔导师的灵魂，与它谈话，它也会提供一些魔导师逃到这里的原因。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1464.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">在兵营深处，可以看到在一个房间内，有</span><span class="rvts20">4</span><span class="rvts21">个牌匾按钮，按照顺序激活这些按钮，可以打开秘密通道的大门。</span>
<br><span class="rvts21">正确的顺序为：心灵（</span><span class="rvts20">Mind</span><span class="rvts21">）</span><span class="rvts20"> - </span><span class="rvts21">身体（</span><span class="rvts20">Body</span><span class="rvts21">）</span><span class="rvts20"> - </span><span class="rvts21">社会（</span><span class="rvts20">Society</span><span class="rvts21">）</span><span class="rvts20"> - </span><span class="rvts21">忠诚（</span><span class="rvts20">Loyalty</span><span class="rvts21">）</span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">心灵</span><span class="rvts20"></span><span class="rvts21">的按钮。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1465.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">身体</span><span class="rvts20"></span><span class="rvts21">的按钮。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1466.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">社会</span><span class="rvts20"></span><span class="rvts21">的按钮。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1467.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">忠诚</span><span class="rvts20"></span><span class="rvts21">的按钮。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1468.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">之后会在一处破旧的厨房中，找到被很多箱子积压的地下室入口。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1469.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">进入地下室，依然需要一道密码用来打开旁边的铁门，它们的按钮藏在了墙上的一些油画后面。移开这些油画，可以找到按钮。</span>
<br><span class="rvts21">按钮的顺序为：心灵（</span><span class="rvts20">Mind</span><span class="rvts21">）</span><span class="rvts20"> - </span><span class="rvts21">社会（</span><span class="rvts20">Society</span><span class="rvts21">）</span><span class="rvts20"> - </span><span class="rvts21">身体（</span><span class="rvts20">Body</span><span class="rvts21">）</span><span class="rvts20"> - </span><span class="rvts21">神圣（</span><span class="rvts20">Divine</span><span class="rvts21">）。如果顺序一旦出错，那么将会使整个地下室的陷阱被激活，并使地下室处于混乱状态。</span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">心灵</span><span class="rvts20"></span><span class="rvts21">按钮位置。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1470.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">社会</span><span class="rvts20"></span><span class="rvts21">按钮位置。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1471.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">身体</span><span class="rvts20"></span><span class="rvts21">按钮位置。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1472.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">该图为</span><span class="rvts20"></span><span class="rvts21">神圣</span><span class="rvts20"></span><span class="rvts21">按钮位置。</span>
<br><span class="rvts21">最后一处按钮激活后，会打开按钮旁的铁门。另外这个房间有一处隐藏的藏宝屋，里面有一些金柱，还可以找到一张</span><span class="rvts20"></span><span class="rvts21">给玩具商人的报告（</span><span class="rvts20">Report on牋the Toyseller</span><span class="rvts21">）</span><span class="rvts20"></span><span class="rvts21">，这将更新</span><span class="rvts20"></span><span class="rvts21">一个工艺人的美梦和噩梦</span><span class="rvts20"></span><span class="rvts21">支线，之后可以找商人，尝试说服他交给你源力项链。</span>
<br><span class="rvts21">另外在该图位置，可以通过走右面的楼梯到底最底层的图书馆，这样可以免除上来就与图书馆中的黑影刺客的打斗。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1473.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">到达图书馆，如果在第二章死神海岸</span><span class="rvts20"></span><span class="rvts21">黑坑洞的黑暗交易</span><span class="rvts20"></span><span class="rvts21">支线中，让雷蒙德坐船逃跑了，他会在这里出现。这里是他所有秘密的藏匿处，你需要杀掉他和他身边的</span><span class="rvts20">3</span><span class="rvts21">个黑影刺客。</span>
<br><span class="rvts21">注意，这场战斗，如果使用群法会使得战斗异常简单，但是由于雷蒙德和他三个刺客周围存在大量的油桶，因此很有可能会引起火灾。这样将会烧掉很多游泳的书籍和证据。</span>
<br><span class="rvts21">一些技巧是必须的：</span>
<br><span class="rvts20">1. </span><span class="rvts21">尽可能不要从上一张图隐藏的地下室入口进入，而是从</span><span class="rvts20"></span><span class="rvts21">社会</span><span class="rvts20"></span><span class="rvts21">按钮旁边的楼梯进入，这样避免刚一开始就进入战斗场面。</span>
<br><span class="rvts20">2. </span><span class="rvts21">尽可能先杀掉雷蒙德，避免他使用火焰法术技能破坏整个图书馆的书籍。三个暗影刺客虽然不会火焰法术，但是他们有一定几率会打破油桶，引起火灾。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1474.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">桌子上会有一封矮人女王的信，将会推进</span><span class="rvts20"></span><span class="rvts21">矮人的秘密</span><span class="rvts20"></span><span class="rvts21">支线。</span>
<br><span class="rvts39"></span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1475.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts21">在大桌子不远处，可以找到一件</span><span class="rvts20"></span><span class="rvts21">烧焦的笔记</span><span class="rvts20"></span><span class="rvts21">，如果这份笔记未被大火彻底烧掉，就可以通过阅读它，了解雷蒙德的计划，并转告林德</span><span class="rvts20"></span><span class="rvts21">凯姆关于魔导师的计划，之后该支线将关闭。</span>
<br><span class="rvts21">另外，该图书馆有很多隐藏的书籍和精良装备，尝试找到它们会提升队伍的作战能力。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



